import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';

import {
  Menu, MenuItem, Box, IconButton,
} from '@material-ui/core';

import ListIcon from '@material-ui/icons/List';

import useStyles from './styles';

const AppMenu = () => {
  const [anchorEl, setAnchorEl] = useState(null);
  const history = useHistory();
  const css = useStyles();

  const openMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClick = (path) => {
    history.push(path);
    setAnchorEl(null);
  };

  return (
    <Box className={css.menuIconContainer}>
      <IconButton aria-controls="app-menu" aria-haspopup="true" onClick={openMenu} color="primary">
        <ListIcon color="secondary" />
      </IconButton>
      <Menu
        id="app-menu"
        anchorEl={anchorEl}
        keepMounted
        classes={{
          list: css.list,
          paper: css.paper,
        }}
        open={Boolean(anchorEl)}
        onClose={handleClick}
      >
        <MenuItem className={css.menuItem} onClick={() => handleClick('/')}>Ghost Picker</MenuItem>
        <MenuItem className={css.menuItem} onClick={() => handleClick('/questions')}>Questions</MenuItem>
      </Menu>
    </Box>
  );
};

export default AppMenu;
