import React, { useState } from 'react';
import { remove, isEmpty } from 'lodash';
import Box from '@material-ui/core/Box';
import Input from '@material-ui/core/Input';

import ghosts from '../../ghostDefs';
import {
  EMF, FINGERPRINTS, TEMPERATURE,
  GHOST_WRITING, SPIRIT_BOX, GHOST_ORB,
} from '../../evidences';

import filterGhostsByEvidences from '../../utils';

import EvidenceButton from './EvidenceButton';
import GhostCard from './GhostCard';

import useStyles from './styles';

const Picker = () => {
  const css = useStyles();
  const [selectedEvidences, setSelectedEvidences] = useState([]);
  const [filteredGhosts, filterGhosts] = useState(ghosts);

  const updateSelectedEvidences = (value, isActive) => {
    const currentState = selectedEvidences;
    if (isActive) {
      currentState.push(value);
      setSelectedEvidences(currentState);
    } else {
      remove(currentState, (el) => el === value);
      setSelectedEvidences(currentState);
    }

    const filtered = filterGhostsByEvidences(ghosts, currentState);
    filterGhosts(filtered);
  };

  return (
    <Box className={css.wrapper}>
      <Box className={css.inputWrapper}>
        <Input placeholder="Ghost's Name" classes={{ root: css.input, underline: css.underline }} />
      </Box>

      <Box className={css.evidencesButtonsWrapper}>
        <EvidenceButton color="red" text={EMF} handleClick={updateSelectedEvidences} />
        <EvidenceButton color="green" text={FINGERPRINTS} handleClick={updateSelectedEvidences} />
        <EvidenceButton color="blue" text={TEMPERATURE} handleClick={updateSelectedEvidences} />
        <EvidenceButton color="purple" text={GHOST_WRITING} handleClick={updateSelectedEvidences} />
        <EvidenceButton color="grey" text={SPIRIT_BOX} handleClick={updateSelectedEvidences} />
        <EvidenceButton color="brown" text={GHOST_ORB} handleClick={updateSelectedEvidences} />
      </Box>
      <Box className={css.ghostsWrapper}>
        {
          filteredGhosts.map((ghost) => (
            <GhostCard data={ghost} key={ghost.name} />
          ))
        }
        {
          isEmpty(filteredGhosts) ? 'No ghosts with such combination of evidences' : null
        }
      </Box>
    </Box>
  );
};

export default Picker;
